// web/pages/admin/index.js
import { useEffect, useState } from 'react';

export default function Admin() {
    const [articles, setArticles] = useState([]);
    const [loading, setLoading] = useState(false);
    const [form, setForm] = useState({ id: null, title: '', summary: '', content: '', status: 'published' });
    const [editing, setEditing] = useState(false);
    const [err, setErr] = useState('');
    const [msg, setMsg] = useState('');

    const API_BASE = process.env.NEXT_PUBLIC_API_BASE || 'http://localhost:3000';

    useEffect(() => { fetchList(); }, []);

    async function fetchList() {
        setLoading(true);
        setErr('');
        try {
            const res = await fetch(`${API_BASE}/api/v1/articles?page=1&limit=50`);
            const j = await res.json();
            setArticles(j.value || []);
        } catch (e) {
            console.error('fetch list err', e);
            setErr('无法获取文章列表');
        } finally { setLoading(false); }
    }

    function handleChange(e) {
        const { name, value } = e.target;
        setForm(s => ({ ...s, [name]: value }));
    }

    function startEdit(a) {
        setForm({ id: a.id, title: a.title || '', summary: a.summary || '', content: a.content || '', status: a.status || 'published' });
        setEditing(true);
    }

    function resetForm() {
        setForm({ id: null, title: '', summary: '', content: '', status: 'published' });
        setEditing(false);
    }

    async function submitForm(e) {
        e.preventDefault();
        setErr(''); setMsg('');
        try {
            const payload = { title: form.title, summary: form.summary, content: form.content, status: form.status, tags: [] };
            let res;
            if (form.id) {
                res = await fetch(`${API_BASE}/api/v1/articles/${form.id}`, {
                    method: 'PUT',
                    headers: { 'Content-Type': 'application/json; charset=utf-8' },
                    body: JSON.stringify(payload)
                });
            } else {
                res = await fetch(`${API_BASE}/api/v1/articles`, {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json; charset=utf-8' },
                    body: JSON.stringify(payload)
                });
            }
            if (!res.ok) throw new Error('服务器错误');
            await fetchList();
            resetForm();
            setMsg('操作成功');
        } catch (e) {
            console.error(e);
            setErr('提交失败');
        }
    }

    async function handleDelete(id, physical = false) {
        if (!confirm('确定删除这篇文章？')) return;
        try {
            const url = `${API_BASE}/api/v1/articles/${id}${physical ? '?physical=1' : ''}`;
            const res = await fetch(url, { method: 'DELETE' });
            if (!res.ok) throw new Error('删除失败');
            await fetchList();
            setMsg('删除成功');
        } catch (e) {
            console.error(e);
            setErr('删除失败');
        }
    }

    // -------- Admin token helpers --------
    // 1) 请求 token 并保存到 localStorage（开发用）
    async function getAndSaveAdminToken() {
        setErr(''); setMsg('');
        try {
            const res = await fetch(`${API_BASE}/api/v1/admin/token`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json; charset=utf-8' },
                body: JSON.stringify({ username: 'admin' }) // dev-only; change for prod
            });
            if (!res.ok) throw new Error('无法获取 token');
            const j = await res.json();
            if (j.token) {
                localStorage.setItem('admin_token', j.token);
                setMsg('token 已保存到 localStorage.admin_token');
            } else {
                throw new Error('响应未包含 token');
            }
        } catch (e) {
            console.error(e);
            setErr('获取 token 失败：' + e.message);
        }
    }

    // 2) 从 localStorage 取 token
    function getSavedToken() {
        return localStorage.getItem('admin_token') || '';
    }

    // -------- Cache control --------
    // 调用后端受保护的 cache refresh endpoint (统一使用 /cache/refresh)
    async function refreshCacheAll(warm = false) {
        setMsg(''); setErr('');
        try {
            const token = getSavedToken();
            if (!token) return setErr('请先获取并保存 admin token（点击 "获取并保存 token"）');
            const r = await fetch(`${API_BASE}/api/v1/admin/cache/refresh`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json; charset=utf-8', Authorization: `Bearer ${token}` },
                body: JSON.stringify({ warm })
            });
            if (!r.ok) {
                const j = await r.json().catch(() => ({}));
                throw new Error(j.error || '刷新失败');
            }
            const j = await r.json();
            setMsg('已刷新缓存: ' + JSON.stringify(j));
            // 刷新本地列表视图
            await fetchList();
        } catch (e) {
            console.error(e);
            setErr('刷新失败：' + e.message);
        }
    }

    return (
        <div style={{ padding: 24, fontFamily: 'system-ui,Arial' }}>
            <h1>Admin — 文章管理</h1>

            <div style={{ marginBottom: 12 }}>
                <button onClick={() => getAndSaveAdminToken()}>获取并保存 admin token（开发用）</button>
                <button onClick={() => refreshCacheAll(false)} style={{ marginLeft: 8 }}>强制刷新缓存（清除）</button>
                <button onClick={() => refreshCacheAll(true)} style={{ marginLeft: 8 }}>清除并预热</button>
                <span style={{ marginLeft: 12, color: '#666' }}>（token 存在于 localStorage.admin_token）</span>
            </div>
            {msg && <div style={{ color: 'green', marginBottom: 12 }}>{msg}</div>}
            {err && <div style={{ color: 'red', marginBottom: 12 }}>{err}</div>}

            <section style={{ marginBottom: 20 }}>
                <h2>{editing ? '编辑文章' : '新建文章'}</h2>
                <form onSubmit={submitForm}>
                    <div style={{ marginBottom: 8 }}>
                        <input name="title" value={form.title} onChange={handleChange} placeholder="标题" style={{ width: '60%' }} />
                    </div>
                    <div style={{ marginBottom: 8 }}>
                        <input name="summary" value={form.summary} onChange={handleChange} placeholder="摘要" style={{ width: '80%' }} />
                    </div>
                    <div style={{ marginBottom: 8 }}>
                        <textarea name="content" value={form.content} onChange={handleChange} placeholder="正文（HTML）" rows={8} style={{ width: '90%' }} />
                    </div>
                    <div style={{ marginBottom: 8 }}>
                        <select name="status" value={form.status} onChange={handleChange}>
                            <option value="published">published</option>
                            <option value="draft">draft</option>
                        </select>
                        <button type="button" onClick={() => { /* optional AI hook */ }} style={{ marginLeft: 12 }}>AI 生成草稿</button>
                    </div>
                    <div>
                        <button type="submit">{editing ? '保存修改' : '发布文章'}</button>
                        <button type="button" onClick={resetForm} style={{ marginLeft: 10 }}>重置</button>
                    </div>
                </form>
            </section>

            <section>
                <h2>文章列表（最多 50）</h2>
                {loading ? <div>加载中…</div> : (
                    <table border="1" cellPadding="6" style={{ borderCollapse: 'collapse' }}>
                        <thead>
                            <tr><th>ID</th><th>标题</th><th>摘要</th><th>操作</th></tr>
                        </thead>
                        <tbody>
                            {articles.map(a => (
                                <tr key={a.id}>
                                    <td>{a.id}</td>
                                    <td style={{ maxWidth: 300 }}>{a.title}</td>
                                    <td style={{ maxWidth: 400 }}>{a.summary}</td>
                                    <td>
                                        <button onClick={() => startEdit(a)}>编辑</button>
                                        <button onClick={() => handleDelete(a.id)} style={{ marginLeft: 8 }}>逻辑删除</button>
                                        <button onClick={() => handleDelete(a.id, true)} style={{ marginLeft: 6 }}>物理删除</button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}
            </section>

            <div style={{ marginTop: 12, color: '#666' }}>
                <p>开发提示：</p>
                <ol>
                    <li>点击 “获取并保存 admin token（开发用）” 会请求 <code>/api/v1/admin/token</code> 并把 token 保存到 <code>localStorage.admin_token</code>。</li>
                    <li>也可以在后端运行 token 请求（PowerShell 或 curl），然后在浏览器开发者工具 Console 粘贴：
                        <pre>localStorage.setItem('admin_token', 'PASTE_TOKEN_HERE')</pre>
                    </li>
                </ol>
            </div>
        </div>
    );
}
